<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-data-table
        :headers="headers"
        :items="colors"
        class="elevation-1"
        :hide-default-footer="true"
      >
        <template v-slot:item.name="{ item }">
          <span :style="`color:${item.hex}`">{{ item.name }}</span>
        </template>
      </v-data-table>
    </v-row>
    <v-row>
      &nbsp;
      <v-divider :inset="true" />
      &nbsp;
    </v-row>
    <v-row>
      <v-spacer />
      <v-btn color="primary" @click="logout">LogOut</v-btn>
      <v-spacer />
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Colors',
  data() {
    return {
      headers: [
        {
          text: 'Цвет',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Хекс значения (Web)', sortable: false, value: 'hex' },
      ],
    };
  },
  beforeMount() {
    if (!this.user) {
      this.$router.push('/');
    }
  },
  computed: {
    ...mapState({
      colors: (state) => state.colors.colors,
      user: (state) => state.user.user || null,
    }),
  },
  methods: {
    logout() {
      this.$store.dispatch('user/logout');
      this.$router.push('/');
    },
  },
};
</script>
